.about {
  position: relative;
  overflow: hidden;
  max-height: 100vh; /* Limita l'altezza al 100% della viewport */
}
  
.about-content {
  position: relative;
  z-index: 1;
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  
  .about-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .about-text {
    flex: 1;
  }
  
  @media (max-width: 768px) {
    .about-image {
      width: 100vw;
      height: auto;
      max-height: 50vh; /* Limita l'altezza su mobile */
    }
  }